      queryValveCurrentData (){
        console.info("组织机构ID："+this.treeNodeInfo.treeNodeOrgId);
        let that = this;
        let ii_company_id ='';
        let ii_child_company_id ='';
        let ii_institute_id ='';
        if(this.pri_detail.treeNodeType !== undefined ){
          let count = this.pri_detail.treeNodeType.length ;
          let type = this.pri_detail.treeNodeType[count-1];
          if(type === "1" ){
            ii_company_id = this.pri_detail.treeNodeIds[count-1] ;
          }else if(type === "2" ){
            ii_child_company_id = this.pri_detail.treeNodeIds[count-1] ;
          }else {
            if (type === "1") {
              ii_company_id = this.pri_detail.treeNodeIds[count - 1];
            } else if (type === "2") {
              ii_child_company_id = this.pri_detail.treeNodeIds[count - 1];
            } else if (type === "3") {
              ii_institute_id = this.pri_detail.treeNodeIds[count - 1];
            } else if (type === "4") {
              if (this.pri_detail.treeNodeType[count - 2] === "3") {
                ii_institute_id = this.pri_detail.treeNodeIds[count - 2];
              } else if (this.pri_detail.treeNodeType[count - 2] === "2") {
                ii_child_company_id = this.pri_detail.treeNodeIds[count - 2];
              } else if (this.pri_detail.treeNodeType[count - 2] === "1") {
                ii_company_id = this.pri_detail.treeNodeIds[count - 2];
              }
            }
            //选择的树节点集合不包含热力所，但是包含公司或者分公司。此时热力所查不到
            if( (ii_company_id !== '' || ii_child_company_id !== '') && ii_institute_id === ''){
              ii_institute_id ="11";
            }
          }
        }

        let _jsondata_ =   {
          perPageSize:this.queryParam.pageSize,
          currentPage:this.queryParam.currentPage,
          ii_institute_name:this.ruleForm.ii_institute_name,
          ii_province:this.ruleForm.ii_province,
          ii_company_id:ii_company_id,
          ii_child_company_id:ii_child_company_id,
          ii_institute_id:ii_institute_id,
          instituteIds : this.instituteIds,
        } ;
        if(this.sortOrderCol!==''){
          _jsondata_.orderby = this.sortOrderCol;
        }
        // const loading = this.$loading({
        //   lock: true,
        //   text: '查询中...',
        //   target: '.main',
        //   spinner: 'el-icon-loading',
        //   background: 'rgba(0, 0, 0, 0)'
        // });
        let p1 = this.customGlobal.postHttpClient(
          this.customGlobal.serverUrl.thermalPowerInstitute_query,
          //this.customGlobal.serverUrl.baseURI,
          this.customGlobal.serverUrl.orgBaseURI,
          {"Content-Type": "application/x-www-form-urlencoded"},
          this.qs.stringify({jsondata:JSON.stringify(_jsondata_)})
        );

        p1.then((res) => {
          console.log("异步调用成功返回:"+JSON.stringify(res));
          if(res.data.status=='success'){
            // loading.close();
            that.tableData = res.data.rows;
            that.queryParam.totalRow = res.data.total;

          }else{
            console.info("查询失败");
            // loading.close();
            that.tableData = [];
            this.$alert(res.data.msg, '查询失败', {
              confirmButtonText: '确定',
              callback: action => {
                console.info('data:'+JSON.stringify(res.data));
                if(res.data.msg==='请登录认证' && res.data.status==='failed'){
                  this.$router.replace({path:'/login'});
                }
              }
            });
          }
        },error=>{
          console.log("异步调用出错:"+JSON.stringify(error));
          // loading.close();
          that.tableData = [];
          this.$alert(JSON.stringify(error.message), '查询出错!', {
            confirmButtonText: '确定',
            callback: action => {
            }
          });
        }).catch((res) => {
          console.log("异步调用失败返回:"+JSON.stringify(res));
          // loading.close();
          that.tableData = [];
          this.$alert(JSON.stringify(res.message), '查询出错', {
            confirmButtonText: '确定',
            callback: action => {
            }
          });
        });
      },
      sortColumn(column) {
        console.log("已经排序项：" + JSON.stringify(column));
        let orderFlag = '';
        if (column.order != null) {
          if (column.order === "descending") {
            orderFlag = 'desc';
          } else if (column.order === "ascending") {
            orderFlag = 'asc';
          }
          console.info("属性名称："+ column.prop+",排序方式："+column.order);

          this.sortOrderCol = "vci." + column.prop + " " + orderFlag;
          console.info("用户选择排序列："+this.sortOrderCol);
          this.queryValveCurrentData ();
        }else{
          this.sortOrderCol = '';
        }
