            if (this.instituteForm.ii_child_company_id !== '') {
              this.treeDataOptions[2] = this.instituteForm.ii_child_company_id;
            }
          }
          this.selectedOptions = [];
          this.instituteForm.ii_uuid = this.customGlobal.genLogicNo("IM");
          this.instituteForm.flag = '1';
          this.instituteDialogFormVisible = true;

        }else{
          this.$alert('请至少选择供热公司', '新增提示', {
            dangerouslyUseHTMLString: true
          });
        }
      },
      handleEditRow(index, row) {
        console.log("修改热力所" + JSON.stringify(row) );
        this.tableDialgTitle = '修改热力所 ['+row.ii_institute_name+']';
        this.instituteForm.ii_institute_name = row.ii_institute_name;
        this.instituteForm.ii_company_id =row.ii_company_id ;   //所属热力公司名称    this.treeNodeInfo.treeNodeCaptions
        this.instituteForm.ii_child_company_id =row.ii_child_company_id;
        this.instituteForm.ii_address = row.ii_address;
        this.treeDataOptions = [];
        if(this.customGlobal.userInfoExt.cui_type !== '0' ) {
          this.treeDataOptions[0] = this.instituteForm.ii_company_id;
          if (this.instituteForm.ii_child_company_id !== '') {
            this.treeDataOptions[1] = this.instituteForm.ii_child_company_id;
          }
        }else{
          this.treeDataOptions[0] ='0';
          this.treeDataOptions[1] =this.instituteForm.ii_company_id;
          if(this.instituteForm.ii_child_company_id !== ''){
            this.treeDataOptions[2] =this.instituteForm.ii_child_company_id;
          }
        }

        this.instituteForm.ii_province = row.ii_province;
        this.instituteForm.ii_city = row.ii_city;
        this.instituteForm.ii_country = row.ii_country;
        this.selectedOptions =[];
        this.selectedOptions[0] = this.instituteForm.ii_province;
        this.selectedOptions[1] = this.instituteForm.ii_city;
        this.selectedOptions[2] = this.instituteForm.ii_country;

        this.instituteForm.ii_longitude = row.ii_longitude;
        this.instituteForm.ii_latitude = row.ii_latitude;
        this.instituteForm.ii_count_area = row.ii_count_area;
        this.instituteForm.ii_contact_name = row.ii_contact_name;
        this.instituteForm.ii_contact_phone = row.ii_contact_phone;
        this.instituteForm.ii_memo = row.ii_memo;
        this.instituteForm.ii_uuid = row.ii_uuid;
        this.instituteForm.flag = '2';
        this.instituteDialogFormVisible = true;

      },
      handleDeleteRow(index, row) {
        console.log("行号："+index+",行内容：" + JSON.stringify(row) );
        if(row.tsi_count !== 0 || row.tpui_count !== 0 ){
          this.$alert('当前要删除的热力公司[<span style="color:red;font-size: 20px;">' + row.ii_institute_name + '</span>]的换热站或用户数量不为零，不可以删除', '删除提示', {
            dangerouslyUseHTMLString: true
          });
        }else {
          this.$confirm('请再次确认是否要删除[<span style="color:red;">' + row.ii_institute_name + '</span>]该行记录?', '删除提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning',
            dangerouslyUseHTMLString: true,
            closeOnClickModal: false,
            closeOnPressEscape: false,

          }).then(() => {
            let _jsondata_ =   {
              ii_uuid:row.ii_uuid,
            } ;
            let p1 = this.customGlobal.postHttpClient(
              this.customGlobal.serverUrl.thermalPowerInstitute_delete,
              //this.customGlobal.serverUrl.baseURI,
              this.customGlobal.serverUrl.orgBaseURI,
              {"Content-Type": "application/x-www-form-urlencoded"},
              this.qs.stringify({jsondata:JSON.stringify(_jsondata_)})
            );
            p1.then((res) => {
              console.log("异步调用成功返回:"+JSON.stringify(res));
              if(res.data.status=='success'){
                this.$message({
                  type: 'success',
                  showClose: true,
                  duration:3000,
                  dangerouslyUseHTMLString: true,
                  message: '<span style="color:black;">基础信息 ['+row.ii_institute_name+'] 删除成功 </span>'
                });
                Bus.$emit('globalMsg',{curid:row.ii_uuid,target:{},method:'3'});
                this.reQueryValveCurrentData();
              }else{
                console.info("删除失败");
                this.$alert(res.data.msg, '删除失败', {
                  confirmButtonText: '确定',
                  callback: action => {
                    console.info('data:'+JSON.stringify(res.data));
                    if(res.data.msg==='请登录认证'){
                      this.$router.push({path:'/login'});
                    }
                  }
                });
              }
            },error=>{
              console.log("异步调用出错:"+JSON.stringify(error));
              this.$alert(JSON.stringify(error.message), '删除出错!', {
                confirmButtonText: '确定',
                callback: action => {
                }
              });
            }).catch((res) => {
              console.log("异步调用失败返回:"+JSON.stringify(res));
              this.$alert(JSON.stringify(res.message), '删除出错', {
                confirmButtonText: '确定',
                callback: action => {
                }
              });
            });
          }).catch((res) => {
            console.log("取消删除:"+JSON.stringify(res));
            this.$message({
              type: 'info',
              showClose: true,
              duration:3000,
              dangerouslyUseHTMLString: true,
              message: '已取消删除'
            });
          });
        }
      },

      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            let that = this;

            let _jsondata_ =   {
              ii_uuid:that.instituteForm.ii_uuid,
              ii_company_id :that.instituteForm.ii_company_id ,
              ii_child_company_id :that.instituteForm.ii_child_company_id ,
              ii_institute_name:that.instituteForm.ii_institute_name,
              ii_address:that.instituteForm.ii_address,
              ii_province:that.instituteForm.ii_province,
              ii_city:that.instituteForm.ii_city,
              ii_country:that.instituteForm.ii_country,
              ii_count_area:that.instituteForm.ii_count_area,
              ii_contact_name:that.instituteForm.ii_contact_name,
              ii_contact_phone:that.instituteForm.ii_contact_phone,
              ii_longitude:that.instituteForm.ii_longitude,
              ii_latitude:that.instituteForm.ii_latitude,
              ii_memo: that.instituteForm.ii_memo,
              flag : that.instituteForm.flag
            } ;
            const loading = this.$loading({
              lock: true,
              text: '提交中...',
              target: '.main',
              spinner: 'el-icon-loading',

              background: 'rgba(0, 0, 0, 0)'
            });
            if(that.instituteForm.flag === '1'){

              let p1 = this.customGlobal.postHttpClient(
                this.customGlobal.serverUrl.thermalPowerInstitute_add,
                //this.customGlobal.serverUrl.baseURI,
                this.customGlobal.serverUrl.orgBaseURI,
                {"Content-Type": "application/x-www-form-urlencoded"},
                this.qs.stringify({jsondata:JSON.stringify(_jsondata_)})
              );

              p1.then((res) => {
                console.log("异步调用成功返回:"+JSON.stringify(res));
                if(res.data.status=='success'){
                  this.instituteDialogFormVisible =false;
                  loading.close();
                  this.$message({
                    type: 'success',
                    showClose: true,
                    duration:3000,
                    dangerouslyUseHTMLString: true,
                    message: '<span style="color:black;">基础信息 ['+that.instituteForm.ii_institute_name+'] 新增成功 </span>'
                  });
                  //热力公司增加树节点,1是同级，2是子级
                  //注意：targetNode中的id是自动生成且唯一,如果是创建第1层的热力公司，currentNodeId一定设置为0,layer一定设置为2
                  if(this.pri_detail.treeNodeIds.length > 2 && this.pri_detail.treeNodeType[2] === '2'){
                    Bus.$emit('globalMsg',{curid:this.pri_detail.treeNodeIds[2],target:{id:that.instituteForm.ii_uuid,caption:that.instituteForm.ii_institute_name,type:3},method:'1',layer:'2'});
                  }else{
                    Bus.$emit('globalMsg',{curid:this.pri_detail.treeNodeIds[1],target:{id:that.instituteForm.ii_uuid,caption:that.instituteForm.ii_institute_name,type:3},method:'1',layer:'2'});
                  }
                  this.queryValveCurrentData();
                }else{
                  console.info("新增失败");
                  loading.close();
                  // this.instituteDialogFormVisible =false;//新增失败关闭dialog
                  this.$alert(res.data.msg, '新增失败', {
                    confirmButtonText: '确定',
                    callback: action => {
                      console.info('data:'+JSON.stringify(res.data));
                      if(res.data.msg==='请登录认证'){
                        this.$router.push({path:'/login'});
                      }
                    }
                  });
                }
              },error=>{
                console.log("异步调用出错:"+JSON.stringify(error));
                loading.close();
                this.$alert(JSON.stringify(error.message), '新增出错!', {
                  confirmButtonText: '确定',
                  callback: action => {
                  }
                });
              }).catch((res) => {
                console.log("异步调用失败返回:"+JSON.stringify(res));
                loading.close();
                this.$alert(JSON.stringify(res.message), '新增出错', {
                  confirmButtonText: '确定',
                  callback: action => {
                  }
                });
              });
            }else{
              let p1 = this.customGlobal.postHttpClient(
                this.customGlobal.serverUrl.thermalPowerInstitute_update,
                //this.customGlobal.serverUrl.baseURI,
                this.customGlobal.serverUrl.orgBaseURI,
                {"Content-Type": "application/x-www-form-urlencoded"},
                this.qs.stringify({jsondata:JSON.stringify(_jsondata_)})
              );
              p1.then((res) => {
                console.log("异步调用成功返回:"+JSON.stringify(res));
                if(res.data.status=='success'){
                  this.instituteDialogFormVisible =false;
                  loading.close();
                  this.$message({
                    type: 'success',
                    showClose: true,
                    duration:3000,
                    dangerouslyUseHTMLString: true,
                    message: '<span style="color:black;">基础信息 ['+that.instituteForm.ii_institute_name+'] 修改成功 </span>'
                  });
                  //热力公司增加树节点,1是同级，2是子级
                  //注意：targetNode中的id是自动生成且唯一,如果是创建第1层的热力公司，currentNodeId一定设置为0,layer一定设置为2
                  Bus.$emit('globalMsg',{curid:that.instituteForm.ii_uuid,target:{id:that.instituteForm.ii_uuid,caption:that.instituteForm.ii_institute_name,type:3},method:'2'});
                  this.queryValveCurrentData();
                }else{
                  console.info("修改失败");
                  loading.close();
                  this.$alert(res.data.msg, '修改失败', {
                    confirmButtonText: '确定',
                    callback: action => {
                      console.info('data:'+JSON.stringify(res.data));
                      if(res.data.msg==='请登录认证'){
                        this.$router.push({path:'/login'});
                      }
                    }
                  });
                }
              },error=>{
                console.log("异步调用出错:"+JSON.stringify(error));
                loading.close();
                this.$alert(JSON.stringify(error.message), '修改出错!', {
                  confirmButtonText: '确定',
                  callback: action => {
                  }
                });
              }).catch((res) => {
                console.log("异步调用失败返回:"+JSON.stringify(res));
                loading.close();
                this.$alert(JSON.stringify(res.message), '修改出错', {
                  confirmButtonText: '确定',
                  callback: action => {
                  }
                });
              });
            }
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      removeTreeNode (node, data) {
        const parent = node.parent
        const children = parent.data.detail || parent.data
        const index = children.findIndex(d => d.id === data.id)
        children.splice(index, 1)
      },
      initTreeDataStatus(h, { node, data, store }) {

        if(JSON.stringify(this.treeDataStatus)==="{}"){
            return (
              < span >
              < span > {data.caption} < /span>
              < /span>
          )
        }else{
          let find = false;
          for (let k of Object.keys(this.treeDataStatus)) {
            if(data.id===k){
              //console.info(k+",显示"+data.caption );
                  return(
                    <span >
                    <span>{data.caption }</span>
                    </span>
                )
              find = true;
              break;
            }else{

            }
          }
          if(find === false){
            this.removeTreeNode( node,data);
          }
        }
      },
      //热力所重新装载树
      repackaging(){
        if( this.customGlobal.userInfoExt.cui_type !== '0' ) {
          for (let typeO = 0; typeO < this.treeData.length; typeO++) {
            if (this.treeData[typeO] !== undefined && this.treeData[typeO].detail !== undefined) {
              for (let typeT = 0; typeT < this.treeData[typeO].detail.length; typeT++) {
                //删除热力公司下的热力所 换热站
                if (this.treeData !== undefined && this.treeData[typeO].detail[typeT].type > 2) {
                  this.treeData[typeO].detail.splice(typeT, 1);
                  typeT--;
                }
                //删除热力分公司下的热力所、换热站
                if (this.treeData !== undefined && this.treeData[typeO].detail !== undefined && this.treeData[typeO].detail[typeT].type < 3) {
                  delete this.treeData[typeO].detail[typeT].detail;
                }
              }
            }
          }
        }else{
          for(let typeO = 0 ; typeO < this.treeData[0].detail.length ; typeO++){
            if(this.treeData[0].detail[typeO] !== undefined && this.treeData[0].detail[typeO].detail !== undefined ){
              for(let typeT = 0;typeT < this.treeData[0].detail[typeO].detail.length ;typeT++){
                //删除热力公司下的热力所 换热站
                if(this.treeData[0].detail !== undefined && this.treeData[0].detail[typeO].detail[typeT].type > 2 ){
                  this.treeData[0].detail[typeO].detail.splice(typeT,1);
                  typeT--;
                }
                //删除热力分公司下的热力所、换热站
                if(this.treeData[0].detail !== undefined && this.treeData[0].detail[typeO].detail !== undefined && this.treeData[0].detail[typeO].detail[typeT].type < 3 ){
                  delete this.treeData[0].detail[typeO].detail[typeT].detail;
                }
              }
            }
          }

        }
      },
      initOrgTemplateInfo(){
        let p1 = this.customGlobal.postHttpClient(
          this.customGlobal.serverUrl.postQueryOrganTemplate,
          //this.customGlobal.serverUrl.baseURI,
          this.customGlobal.serverUrl.orgBaseURI,
          {"Content-Type": "application/x-www-form-urlencoded"},
          this.qs.stringify({jsondata:JSON.stringify({config:'all',username:this.customGlobal.userInfoExt.cui_login_id})})
        );

        p1.then((res) => {
          //console.log("异步调用成功返回:"+JSON.stringify(res));
          if(res.data.status=='success'){
            if( this.customGlobal.userInfoExt.cui_type !== '0' ){
              this.treeData = JSON.parse(res.data.organ).detail;
            }else{
              this.treeData =  [JSON.parse(res.data.organ)];
            }
            this.treeDataStatus = JSON.parse(res.data.orgkey);
            this.repackaging();

          }else{
            console.info("机构树查询失败");
            this.treeData = [ JSON.parse(res.data.organ) ];
            this.treeDataStatus = JSON.parse(res.data.orgkey);
          }
        },error=>{
          console.log("异步调用出错:"+JSON.stringify(error));
          this.$alert(JSON.stringify(error.message), '机构树查询出错!', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        }).catch((res) => {
          console.log("异步调用失败返回:"+JSON.stringify(res));
          this.$alert(JSON.stringify(res.message), '机构树查询出错', {
            confirmButtonText: '确定',
            callback: action => {
            }
          });
        });

      }
    }
  }
</script>

